//主页
import React from 'react';
import {Grid,Row,Col} from '../../components/grid/Grid';
import Slider from '../../components/slider/Slider';
import Mlist from '../../components/list/Mlist';
import Card from '../../components/card/Card';
import Panel from '../../components/panel/Panel';
import Tabs from '../../components/tabs/Tabs';


const IMAGE_DATA_1 = [
  {
    src: require('./images/1.jpg'),
    alt: 'images-1',
  },
  {
    src: require('./images/2.jpg'),
    alt: 'images-2',
  }
];
const IMAGE_DATA_2 = [
  {
    src: require('./images/东山湾生态浮标.jpg'),
    alt: '东山湾生态浮标',
  },
  {
    src: require('./images/厦门中心站完成福建.jpg'),
    alt: '厦门中心站完成福建',
  },
  {
    src: require('./images/厦门中心站.jpg'),
    alt: '厦门中心站',
  }
];
const LIST_DATA=[
  {
    content:'厦门中心站完成福建南部海域八月综合“体检”任务',
    tag:'2016-10-24'
  },
  {
    content:'东山湾生态浮标完成大修并重新布放',
    tag:'2016-08-10'
  },
  {
    content:'莆田海洋站站部正式动工',
    tag:'2016-08-04'
  },
  {
    content:'厦门中心站第三党支部开展“两学一做”专题党课教育',
    tag:'2016-08-04'
  },
  {
    content:'厦门中心站创新培训方式凝聚团队精神',
    tag:'2016-07-04'
  },
  {
    content:'“苏迪罗”强台风登陆福建，厦门中心站精心部署全力应对',
    tag:'2016-07-04'
  }
];

class home extends React.Component{
  constructor(props) {
      super(props);
  }
  render() {
      return  <Row style={{minHeight:"604px",marginTop:"20px"}}>
                <Row>
                  <Col width={[4,7]}>
                    <Slider items={IMAGE_DATA_2} speed={1.2} delay={2.1} pause={true} autoplay={true} dots={true} arrows={false} istext={true} height={"300px"}/>
                  </Col>
                  <Col width={[3,7]}>
                    <Card title={'工作动态'} card_body={{marginTop:"102px"}} card_title={{background:"#1ea0ff",height:"32px",lineHeight:"32px"}}>
                      <Mlist list={LIST_DATA} />
                    </Card>
                  </Col>
                </Row>
                <Row>
                  <Panel>
                    <Tabs>
                      <div name="测试1">test1</div>
                      <div name="测试2">test2</div>
                      <div name="测试3">test3</div>
                    </Tabs>
                    <Col width={[18,30]}>
                      <Card title={'数值预报'} icon_url={require('./images/szyb_icon.png')} more_img={require('./images/more.png')} card_title={{background:"#1ea0ff",height:"58px",lineHeight:"58px"}}>
                      </Card>
                    </Col>
                    <Col width={[11,30]} offset={[1,30]}>
                      <Card title={'预警报单'} icon_url={require('./images/szyb_icon.png')} more_img={require('./images/more.png')} card_title={{background:"#e64f3c",height:"58px",lineHeight:"58px"}}>
                      </Card>
                    </Col>
                  </Panel>
                </Row>
                <Row>
                  <Col>
                    <Slider
                        items={IMAGE_DATA_1}
                        speed={1.2}
                        delay={2.1}
                        pause={true}
                        autoplay={true}
                        dots={true}
                        arrows={false}
                        height={"170px"}
                      />
                  </Col>
                </Row>
              </Row>
      }
};
export default home;



